import axios from '../utils/http';
export async function getSpecsList(params) {
  const res = await axios.get('/specslist', {
    params
  });

  if (res.code === 200) {
    return res.list
  }
  return false
}

export async function getSpecsTotal() {
  const res = await axios.get('/specscount');
  if (res.code === 200) {
    return res.list[0].total;
  }
  return false
}



