import { getMemberList } from '../../request/member';
export default {
  namespaced: true,
  state() {
    return {
      memberList: []
    }
  },
  mutations: {
    memberListInit(state, list) {
      state.memberList = list;
    }
  },
  actions: {
    getMemberListAction({ commit }) {
      getMemberList().then(res => {
        if (res) {
          commit('memberListInit', res)
        }
      })
    }
  }
}


