import Vue from 'vue'
import Router from 'vue-router'
const Login = () => import('@/pages/Login');
const Layout = () => import('@/pages/Layout');
const Menu = () => import('@/pages/menu/Index')
const Role = () => import('@/pages/role/Index')
const Admin = () => import('@/pages/admin/Index');
const Home = () => import('@/pages/Home');
const Cate = () => import('@/pages/cate/Index');
const Specs = () => import('@/pages/specs/Index');
const Goods = () => import('@/pages/goods/Index');
const Member = () => import('@/pages/member/Index');
const Banner = () => import('@/pages/banner/Index');
const Seck = () => import('@/pages/seck/Index');
Vue.use(Router)
const routes = [{
  path: '/login',
  name: 'Login',
  component: Login,
  meta: {
    title: '管理员登录'
  }
},
{
  path: '/',
  name: 'Layout',
  component: Layout,
  meta: {
    title: '小u商城后台管理系统'
  },
  redirect: '/home',
  children: [{
    path: '/home',
    name: 'Home',
    component: Home,
    meta: {
      title: '管理中心'
    },
  },
  {
    path: '/menu',
    name: 'Menu',
    component: Menu,
    meta: {
      title: '菜单管理'
    },
  },
  {
    path: '/role',
    name: 'Role',
    component: Role,
    meta: {
      title: '角色管理'
    },
  },
  {
    path: '/admin',
    name: 'admin',
    component: Admin,
    meta: {
      title: '管理员管理'
    }
  },
  {
    path: '/cate',
    name: 'cate',
    component: Cate,
    meta: {
      title: '分类管理'
    }
  },
  {
    path: '/specs',
    name: 'specs',
    component: Specs,
    meta: {
      title: '规格管理'
    }
  },
  {
    path: '/goods',
    name: 'goods',
    component: Goods,
    meta: {
      title: '商品管理'
    }
  },
  {
    path: '/member',
    name: 'member',
    component: Member,
    meta: {
      title: '会员管理'
    }
  },
  {
    path: '/banner',
    name: 'banner',
    component: Banner,
    meta: {
      title: '轮播图管理'
    }
  },
  {
    path: '/seck',
    name: 'seck',
    component: Seck,
    meta: {
      title: '秒杀活动'
    }
  }
  ]
},
]
const router = new Router({
  routes
})
//路由导航守卫
router.beforeEach(function (to, from, next) {
  if (to.path != '/login') {
    //读取本地存储
    const loginInfo = JSON.parse(localStorage.getItem('loginInfo') || '{}');
    // 登录凭证
    if (!loginInfo.token) {
      return next('/login')
    }
    //校验访问权限
    // const menusUrl = loginInfo.menus_url;
    // menusUrl.push('/home');
    // menusUrl.push('/');
    // if (menusUrl.indexOf(to.path) === -1) {
    //   return next('/home')
    // }
  }
  next();
  //动态设置页面标题
  document.title = to.meta.title;
})

export default router;



