import { getSeckList } from '../../request/seck';
export default {
  namespaced: true,
  state() {
    return {
      seckList: []
    }
  },
  mutations: {
    seckListInit(state, list) {
      state.seckList = list
    }
  },
  actions: {
    getSeckListAction({ commit }) {
      getSeckList().then(res => {
        if (res) {
          commit('seckListInit', res)
        }
      })
    }
  }
}


