//导入数据请求方法
import {
  getCateList
} from "../../request/cate";
export default {
  namespaced: true,
  state() {
    return {
      cateList: []
    };
  },
  mutations: {
    cateListInit(state, list) {
      state.cateList = list;
    }
  },
  actions: {
    getCateListAction({
      commit
    }) {
      getCateList().then(list => {
        if (list) {
          commit("cateListInit", list);
        }
      });
    }
  }
};



