import { getBannerList } from '../../request/banner';
export default {
  namespaced: true,
  state() {
    return {
      bannerList: []
    }
  },
  mutations: {
    bannerListInit(state, list) {
      state.bannerList = list;
    }
  },
  actions: {
    getBannerListAction({ commit }) {
      getBannerList().then(res => {
        if (res) {
          commit('bannerListInit', res)
        }
      })
    }
  }
}


