import {
  getRoleList
} from '../../request/role';
export default {
  namespaced: true,
  state() {
    return {
      //角色列表
      roleList: []
    }
  },
  mutations: {
    //初始化角色列表
    roleListInit(state, list) {
      state.roleList = list;
    }
  },
  actions: {
    //请求角色列表
    getRoleListAction({
      commit
    }) {
      getRoleList().then(res => {
        if (res) {
          commit('roleListInit', res)
        }
      })
    }
  },
  getters: {

  }

}



